@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(14 13 17);
}

input[type="text"]::-webkit-input-placeholder,
input[type="email"]::-webkit-input-placeholder,
input[type="number"]::-webkit-input-placeholder {
  color: #f8f8f4;
}

input[type="text"],
input[type="email"],
input[type="number"] {
  color: #f8f8f4;
  background-color: #44475a !important;
  border-color: #bd93f9 !important;
}

.form-control {
  background-color: #44475a !important;
  color: #f8f8f4 !important;
  border-color: #bd93f9;
}

a:link {
  text-decoration: none;
}

.custom-card {
  background-color: #282a36;
  border-radius: 20px;
  border: none;
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.5);
  padding: 30px;
}

.custom-title {
  font-weight: 600;
  font-size: 26px;
  color: #f8f8f2;
  text-align: center;
}

input.custom-input,
select.custom-input {
  background-color: #282a36 !important;
  color: #f8f8f2;
  border-radius: 12px;
  border: 1px solid #6272a4;
  padding: 14px 20px;
  transition: border-color 0.3s ease;
}

.custom-input:focus {
  border-color: #bd93f9 !important;
}

input.input-error,
select.input-error {
  border-color: #ff5555 !important;
}

.custom-label {
  position: absolute;
  top: -10px;
  left: 20px;
  font-size: 12px;
  color: #bd93f9;
  padding: 0 5px;
  background-color: #282a36;
}

.custom-btn {
  background-color: #bd93f9;
  border-radius: 15px;
  padding: 12px;
  font-size: 18px;
  font-weight: 600;
  box-shadow: 0 10px 20px rgba(189, 147, 249, 0.5);
  transition: all 0.3s ease;
}

.custom-btn:hover {
  background-color: #ab70ff;
}

.card {
  border-radius: 15px;
  border: none;
  background-color: #44475a;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 4px 20px;
  transition: transform 0.3s, box-shadow 0.3s;
}
.card:hover {
  box-shadow: rgba(0, 0, 0, 0.8) 0px 8px 30px;
}
.profile-image {
  width: 120px;
  height: 120px;
  object-fit: cover;
  border-radius: 50%;
  border: 4px solid #bd93f9;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 10px;
}
.bio {
  font-size: 0.9rem;
  color: #f8f8f2;
}
.social-links a {
  color: #bd93f9;
}
.social-links a:hover {
  color: #ff79c6;
}
.btn-custom {
  background-color: #bd93f9;
  color: #282a36;
}
.btn-custom:hover {
  background-color: #ff79c6;
}
.text-light {
  color: #f8f8f2;
}

.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-back {
  position: absolute;
  z-index: 999999;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background-color: #000000a1;
}

.popup {
  position: absolute;
  z-index: 999999;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
  width: 413px;
  height: 176px;
  color: white;
  background-color: #44475a;
  border-radius: 8px;
  padding: 20px;
  padding-top: 42px;
  text-align: center;
}

.button-container {
  margin-top: 20px;
}

button {
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin: 0 5px;
  transition: background-color 0.3s, transform 0.2s;
}

.cancel-button {
  background-color: #bd93f9;
  color: #282a36;
}

.delete-button {
  background-color: #ff5555;
  color: #fff;
}

.cancel-button:hover {
  background-color: #a76bb4;
}

.delete-button:hover {
  background-color: #ff3c3c;
}

.cancel-button:active,
.delete-button:active {
  transform: scale(0.95);
}
